body {
  margin: 0;
  padding: 0;
  font-family: "montserrat", sans-serif;
}

.services {
  text-align: center;
}

.services h1 {
  display: inline-block;
  text-transform: uppercase;
  border-bottom: 4px solid rgba(78, 199, 207, 1);
  font-size: 20px;
  padding-bottom: 10px;
  margin-top: 40px;
}

.cen {
  max-width: 1200px;
  margin: auto;
  overflow: hidden;
  padding: 20px;
}

.service {
  display: inline-block;
  width: calc(100% / 3);
  margin: 0 -2px;
  padding: 20px;
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.4s;
}

.service:hover {
  background: #ddd;
}

.service i {
  font-size: 34px;
  margin-bottom: 30px;
}

.service h2 {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 500;
  margin: 0;
}

.service p {
  color: gray;
  font-size: 15px;
  font-weight: 500;
}

.fa-anchor,
.fa-apple-alt {
  color: rgb(11, 193, 230);
}

.fa-android,
.fa-archway {
  color: rgba(21, 130, 196, 1);
}

.fa-angellist,
.fa-angry {
  color: rgba(78, 199, 207, 1);
}

@media screen and (max-width: 800px) {
  .service {
    width: 50%;
  }
}
@media screen and (max-width: 500px) {
  .service {
    width: 100%;
  }
}
